.root {
	display: -webkit-inline-box;
	display: -ms-inline-flexbox;
	display: inline-flex;
	color: var(--text);
	color: var(--text);
	-webkit-transition: color var(--ui-dpd-1) var(--ui-easing),
	-webkit-transform var(--ui-dpd-1) var(--ui-easing);
	transition: color var(--ui-dpd-1) var(--ui-easing),
	-webkit-transform var(--ui-dpd-1) var(--ui-easing);
	transition: color var(--ui-dpd-1) var(--ui-easing),
	transform var(--ui-dpd-1) var(--ui-easing);
	transition: color var(--ui-dpd-1) var(--ui-easing),
	transform var(--ui-dpd-1) var(--ui-easing),
	-webkit-transform var(--ui-dpd-1) var(--ui-easing);
	transition: color var(--ui-dpd-1) var(--ui-easing),
	transform var(--ui-dpd-1) var(--ui-easing);
	z-index: 2;
}
.root.primary, .root.secondary {
	color: var(--ui-bg);
	color: var(--ui-bg);
}
.muted {
	color: var(--text-muted);
	color: var(--text-muted);
}
/*
	Prevent visibility of selection for 2 fast ui clicks [aka doubleclick],
	e.g. fast toggle, checkbox etc. TODO
*/
.root::-moz-selection {
	background-color: transparent;
}
.root::selection {
	background-color: transparent;
}
.required::after {
	content: '*';
	color: var(--ui-error);
	color: var(--ui-error);
}
