}
.disabled.filled .inner::after {
	background-color: var(--ui-menu-border);
}
*/
.root label ~ .inner {
	margin-left: var(--spacing);
	margin-left: var(--spacing);
}
.inner::before {
	content: "";
	opacity: 0;
	-webkit-transform: scale(0, 0);
	        transform: scale(0, 0);
	border-radius: 50%;
	background-color: var(--ui-accent);
	background-color: var(--ui-accent);
}
/* Checked styles */
/*
.root:not(:hover) .input:checked ~ .inner:not(.parentSchema)::after {
	border-color: var(--ui-border);
}
.root:not(:hover) .input:not(:checked) ~ .inner:not(.parentSchema)::after {
	border-color: var(--ui-bg);
}
*/
/* prevent ripple animation on page load */
.enabled.normal .input:not(:hover) ~ .inner::before,
.enabled.toggle .input:not(:hover) ~ .inner .toggleSwitch::before {
	visibility: hidden;
	-webkit-transition: all 0.1s var(--ui-dpd-16);
	transition: all 0.1s var(--ui-dpd-16);
	transition: all 0.1s var(--ui-dpd-16);
}
/* switch on/off ripple animation */
@-webkit-keyframes ripple {
  0% { opacity: var(--checkbox-ripple-alpha); opacity: var(--checkbox-ripple-alpha); -webkit-transform: scale(1,1); transform: scale(1,1); }
  100% { opacity: 0; -webkit-transform: scale(var(--checkbox-ripple-size),var(--checkbox-ripple-size)); transform: scale(var(--checkbox-ripple-size),var(--checkbox-ripple-size)); transform: scale(var(--checkbox-ripple-size),var(--checkbox-ripple-size)); }
}
@keyframes ripple {
  0% { opacity: var(--checkbox-ripple-alpha); opacity: var(--checkbox-ripple-alpha); -webkit-transform: scale(1,1); transform: scale(1,1); }
  100% { opacity: 0; -webkit-transform: scale(var(--checkbox-ripple-size),var(--checkbox-ripple-size)); transform: scale(var(--checkbox-ripple-size),var(--checkbox-ripple-size)); transform: scale(var(--checkbox-ripple-size),var(--checkbox-ripple-size)); }
}
.enabled.normal .input:checked ~ .inner::before,
.enabled.toggle .input:checked ~ .inner .toggleSwitch::before {
	background-color: var(--ui-accent);
	background-color: var(--ui-accent);
	-webkit-animation: ripple var(--ui-dpd-16) var(--ui-easing) 0s 1 normal none;
	        animation: ripple var(--ui-dpd-16) var(--ui-easing) 0s 1 normal none;
	animation: ripple var(--ui-dpd-16) var(--ui-easing) 0s 1 normal none;
}
@-webkit-keyframes rippleOff {
  0% { opacity: var(--checkbox-ripple-alpha); opacity: var(--checkbox-ripple-alpha); -webkit-transform: scale(1,1); transform: scale(1,1); }
  100% { opacity: 0; -webkit-transform: scale(var(--checkbox-ripple-size),var(--checkbox-ripple-size)); transform: scale(var(--checkbox-ripple-size),var(--checkbox-ripple-size)); transform: scale(var(--checkbox-ripple-size),var(--checkbox-ripple-size)); }
}
@keyframes rippleOff {
  0% { opacity: var(--checkbox-ripple-alpha); opacity: var(--checkbox-ripple-alpha); -webkit-transform: scale(1,1); transform: scale(1,1); }
  100% { opacity: 0; -webkit-transform: scale(var(--checkbox-ripple-size),var(--checkbox-ripple-size)); transform: scale(var(--checkbox-ripple-size),var(--checkbox-ripple-size)); transform: scale(var(--checkbox-ripple-size),var(--checkbox-ripple-size)); }
}
.enabled.normal .input:not(:checked) ~ .inner::before {
	background-color: var(--checkbox-off);
	background-color: var(--checkbox-off);
	-webkit-animation: rippleOff var(--ui-dpd-16) var(--ui-easing) 0s 1 normal none;
	        animation: rippleOff var(--ui-dpd-16) var(--ui-easing) 0s 1 normal none;
	animation: rippleOff var(--ui-dpd-16) var(--ui-easing) 0s 1 normal none;
}
/* invalid styles */
.invalid .inner::after {
	border-color: var(--ui-error);
	border-color: var(--ui-error);
}
.invalid .inner::before {
	color: var(--ui-error);
	color: var(--ui-error);
}
/* valid style */
.valid .inner::after {
	border-color: var(--ui-success);
	border-color: var(--ui-success);
}
.valid .inner::before {
	color: var(--ui-success);
	color: var(--ui-success);
}
