:root {
	/* Spacing */
	--grid-base: 8px;
	--spacing-regular: 8px;
	--spacing-regular: var(--grid-base);
	--spacing-large: calc(8px * 2);
	--spacing-large: calc(var(--grid-base) * 2);
	--touch-target-min: 48px;

	/* Fonts */
	--font-size-base: 16px;
	--font-size-small: 14px;
	--font-size-title: 20px;
	--font-size-icon: 24px;
	--line-height-base: 24px;
	--icon-font-family: 'dojo2BaseTheme';

	/* Color hex values */
	--dojo-white: #ffffff;
	--dojo-light-grey: #f4f6f7;
	--dojo-grey: #d6dde2;
	--dojo-dark-grey: #5c6c7c;
	--dojo-black: #000000;
	--dojo-blue: #006be6;
	--dojo-red: #eb1313;
	--dojo-green: #188701;

	/* Color usage names */
	--color-text-primary: #000000;
	--color-text-primary: var(--dojo-black);
	--color-text-faded: #5c6c7c;
	--color-text-faded: var(--dojo-dark-grey);
	--color-text-inverted: #ffffff;
	--color-text-inverted: var(--dojo-white);
	--color-highlight: #006be6;
	--color-highlight: var(--dojo-blue);
	--color-success: #188701;
	--color-success: var(--dojo-green);
	--color-error: #eb1313;
	--color-error: var(--dojo-red);
	--color-background: #ffffff;
	--color-background: var(--dojo-white);
	--color-background-inverted: #5c6c7c;
	--color-background-inverted: var(--dojo-dark-grey);
	--color-background-faded: #f4f6f7;
	--color-background-faded: var(--dojo-light-grey);
	--color-border: #d6dde2;
	--color-border: var(--dojo-grey);
	--color-border-strong: #5c6c7c;
	--color-border-strong: var(--dojo-dark-grey);
	--color-box-shadow: rgba(0, 0, 0, 0.2);
	--color-box-shadow: rgba(0, 0, 0, 0.2);
	--color-box-shadow-strong: rgba(0, 0, 0, 0.5);
	--color-box-shadow-strong: rgba(0, 0, 0, 0.5);

	/* Border and shadow */
	--box-shadow-dimensions-small: 0 2px 2px 0;
	--box-shadow-dimensions-large: 0 9px 16px 0;
	--border-width: 1px;
	--border-width-emphasized: 2px;

	/* Transitions */
	--transition-duration: 0.25s;
	--transition-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.root {
	color: #000000;
	color: var(--color-text-primary);
	font-size: 16px;
	font-size: var(--font-size-base);
	line-height: 24px;
	line-height: var(--line-height-base);
}

.root,
.root * {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.main {
	background: #FFF;
	border: 1px solid #d6dde2;
	border: var(--border-width) solid var(--color-border);
	-webkit-box-shadow: 0 9px 16px 0 rgba(0, 0, 0, 0.5);
	        box-shadow: 0 9px 16px 0 rgba(0, 0, 0, 0.5);
	-webkit-box-shadow: var(--box-shadow-dimensions-large) var(--color-box-shadow-strong);
	        box-shadow: var(--box-shadow-dimensions-large) var(--color-box-shadow-strong);
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	height: calc(75 * 8px);
	height: calc(75 * var(--grid-base));
	left: 50%;
	max-height: 100%;
	max-width: 100%;
	position: fixed;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	width: calc(80 * 8px);
	width: calc(80 * var(--grid-base));
	z-index: calc(400 + 1);
	z-index: calc(var(--zindex-dialog) + 1);
}

.underlayVisible {
	background-color: rgba(0, 0, 0, 0.5);
	background-color: var(--color-box-shadow-strong);
	z-index: 400;
	z-index: var(--zindex-dialog);
}

.title {
	border-bottom: 1px solid #d6dde2;
	border-bottom: var(--border-width) solid var(--color-border);
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 auto;
	        flex: 0 0 auto;
	font-size: 20px;
	font-size: var(--font-size-title);
	height: calc(7 * 8px);
	height: calc(7 * var(--grid-base));
	padding: calc(2 * 8px);
	padding: calc(2 * var(--grid-base));
	position: relative;
	text-align: left;
}

.content {
	-webkit-box-flex: 1;
	    -ms-flex: 1 1 auto;
	        flex: 1 1 auto;
	overflow: auto;
	padding: calc(2 * 8px);
	padding: calc(2 * var(--grid-base));
}

.close {
	background: none;
	border: none;
	cursor: pointer;
	font-size: 0;
	padding: 8px;
	padding: var(--grid-base);
	position: absolute;
	right: calc(2 * 8px);
	right: calc(2 * var(--grid-base));
	top: 50%;
	-webkit-transform: translateY(-50%);
	        transform: translateY(-50%);
}

.close i {
	font-size: 20px;
	font-size: var(--font-size-title);
}
