.rootFixed {
	display: inline-block;
}

.wrapperFixed {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	position: relative;
}

.fillFixed {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	top: 0;
}

.trackFixed {
	-webkit-box-ordinal-group: 3;
	    -ms-flex-order: 2;
	        order: 2;
	position: relative;
	width: 100%;
}

.outputTooltip output {
	position: absolute;
}

/* Make native input invisible */

.nativeInput {
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	background: transparent;
	border: 0 none;
	color: transparent;
	font-size: inherit;
	left: 0;
	margin: 0px;
	overflow: visible;
	padding: 0px;
	position: absolute;
	top: 0;
	width: 100%;
	z-index: 1;
}

/* invisible native track */

.nativeInput::-ms-track {
	appearance: none;
	background: transparent;
	border: 0 none;
	color: transparent;
	height: 100%;
	margin: 0px;
	overflow: visible;
	padding: 0px;
	width: 100%;
}

.nativeInput::-moz-range-track {
	-moz-appearance: none;
	     appearance: none;
	background: transparent;
	border: 0 none;
	color: transparent;
	height: 100%;
	margin: 0px;
	overflow: visible;
	padding: 0px;
	width: 100%;
}

.nativeInput:focus::-webkit-slider-runnable-track {
	background: transparent;
	border: transparent;
}

/* thumb needs to be invisible and same size as custom thumb */

.nativeInput::-ms-thumb {
	border-radius: 50%;
	border: 0 none;
	background: transparent;
	cursor: pointer;
}

.nativeInput::-moz-range-thumb {
	border-radius: 50%;
	border: 0 none;
	background: transparent;
	cursor: pointer;
}

.nativeInput::-webkit-slider-thumb {
	-webkit-appearance: none;
	        appearance: none;
	border-radius: 50%;
	border: 0 none;
	background: transparent;
	cursor: pointer;
}

/* microsoft IE10+ and Edge - eliminates default fills before and after thumb */

.nativeInput::-ms-fill-lower {
	background: transparent;
	border: 0 none;
}

.nativeInput::-ms-fill-upper {
	background: transparent;
	border: 0 none;
}

.nativeInput::-ms-tooltip {
	display: none;
}
